@charset "UTF-8";
@font-face{
  font-family:'Helvetica Medium';
  src:url(../Archives/fonts/helvmn.ttf);
}
@font-face{
  font-family:'Anke';
  src:url(../Archives/fonts/Anke.otf);
}
@font-face{
  font-family:'Helvetica Regular';
  src:url(../Archives/fonts/helvmn.ttf);
}

*{
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
    /*outline: 1px solid rgb(255, 196, 0);*/
}
body {
    margin: 0;
    padding: 0;
    margin-top: -16px;
}
/*navigator*/
.logo-spaceag{
    position: absolute;
    width: 2px;
    left: 6%;
}
img.logo-spaceag.navbar-brand{
    width: 200px !important;
}
.navbar-brand {
    float: left;
    height: auto; 
    margin-top: -14px;
}
.navbar-inverse{
    position: absolute;
    background-color: transparent;
    border-color: transparent;
    top: 2%;
}
.navbar-fixed-bottom .navbar-fixed-top{
    position: absolute;
}
.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover{
    background-color: transparent;
}
.navbar-inverse .navbar-toggle{
    border-color: transparent;
}
.navbar-inverse .navbar-collapse, .navbar-inverse .navbar-form {
    border-color: transparent;
}
ul.nav.navbar-nav.navbar-right{
    margin-right: -124px;
}
/*header*/
.header{
    background-image: url("../Img/fondo.jpg");
    background-size: cover;
    width: 100%;
    height: 100vh;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: left;
    display: table;
}

.spaceag{
    font-family: 'Anke';
    color: #fff;
    font-size: 5rem;
    margin-left: 5%;
    letter-spacing: 2px;
    font-weight: bold;
    padding-bottom: 1%;
}
.title{
    display: table-cell;
    vertical-align: middle;
    height: 100vh;
}
.subtitle{
    font-size: 32px;
    padding-bottom: 0;
}

