/*sections*/
.section-nav{
    background: #EDEEF0;
    padding: 0px 0 20px 0;
    margin-top: 5%;
}
h1.sections{
    font-family: 'Anke';
    color: #036920;
    letter-spacing: 2px;
    text-align: center;
    font-size: 4.4rem;
    font-weight: 600;
}
h1.contact{
    font-size: 4rem;
}
h2{
    font-family: Anke;
    color: #036920;
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 3.1rem;
}
h3{
    font-family: "Helvetica Medium";
    color: #69af22;
    font-size: 2.2rem;
    letter-spacing: 2px;
}
p.textAbout{
    font-family: "Helvetica Medium"; 
    color: #7c7c7e;
    font-size: 1.9rem;
}
div.solutions.row{
    background: #EDEEF0;
}
div.contenedor{
    height: 400px;
    display: table;
  }
div.contenido{
    display: table-cell;
    vertical-align: middle;
}
.col-md-6 {
    padding: 0 10%;
}
/*counter*/
.counterDigitos div.row{
    text-align: center;
}
.counterDigitos{
    background-image: url("../Img/arandanos.jpg");
    height: auto;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}
.CounterDig{
    background: rgba(0, 0, 0, 0.514);
    margin-bottom: 0;
}
span.digitos{
    font-size: 41px;
    font-family: Anke;
    color: #fff;
    font-weight: bold;
}
h3.counters{
    color: #e6e6e6;
    font-family: Anke;
    margin-bottom: 20%;
}
div.boxCounter{
    margin-top: 4%;
    margin-bottom: -58px;
}
/*customers*/
ul.customers{
    text-align: center;
    -webkit-padding-start: 0 !important;
}
.customers li{
    display: inline;
    padding: 0 20px;
}
.customers  img{
    width: 10%;
}
.customers img.acp{
    width: 7%;
}

/*media*/
ul.media{
    text-align: center;
    -webkit-padding-start: 0 !important;
}
.media:first-child{
    margin-top: 55px;
}
.media{
    text-align: center;
}
.media li{
    display: inline;
    padding: 0 20px;
}
.media li img{
    width: 10%;
}
.media li img:hover{
    -webkit-filter: opacity(.5);
            filter: opacity(.5);
}
img.noticeSpace{
    vertical-align: inherit;
    border-radius: 15px;
    margin-bottom: 15px;
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
}
.btn-primary {
    color: #fff;
    background-color: #036920;
    border-color: #036920;
    width: 100%;
    margin-bottom: 30px;
    letter-spacing: 2px;
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
}
.btn-primary:focus,
.btn-primary.focus {
    color: #e6e6e6;
    background-color: #69af22;
    border-color: #69af22;
}
.btn-primary:hover {
    color: #e6e6e6;
    background-color: #69af22;
    border-color: #69af22;
}
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
    color: #fff;
    background-color: #69af22;
    border-color: #69af22;
}
button.view{
    margin-top: 15px;
}
/*contact*/
div.contacto.row{
    background: #EDEEF0;
    padding-bottom: 5%;
}
h3.demo{
    color: #717171;
    text-align: center;
    margin-bottom: 5%;
    text-transform: uppercase;
    font-family:'Helvetica Medium';
    font-size: 12px;
}
div.dato{
    color: #036920;
    font-weight: 500;
    font-family: Anke;
    letter-spacing: 2px;
    font-size: 17px;
}
textarea.form-control{
    height: 200px;
}
.btnSend {
    margin: 12px auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center; 
    background: #036920;
    border: inherit;
    color: #e6e6e6;
    border-radius: 10px;
    font-family: Anke;
    width: 100%;
    padding: 7px 0;
    letter-spacing: 2px;
    font-size: 15px;
}
/*Blog*/
h3.blog{
    color: #036920;
    font-size: 32px;
    letter-spacing: 3px;
    text-align: center;
    margin-bottom: 5%;
    text-transform: uppercase;
    font-family: Anke;
    font-weight: 600;
}
div.blog.row{
    background-image: url('../Img/Header.jpg');
    height: 350px;
    background-attachment: fixed;
    background-position: center;
}
a.blog{
    color: #717171;
    text-decoration: underline;
    text-align: center;
    display: block;
    font-size: 16px;
}
.blog-container{
    background: #e6e6e6;
    width: 30%;
    display: block;
    margin: auto;
    margin-top: 100px;
    padding:3px 0 15px 0;
    letter-spacing: 2px;
}
  /*footer*/
.footer {
    background-color: #036920;
    height: 100px;
    color: #ffffff;
    font-family: "Helvetica Medium"; 
    margin-bottom: 0 !important;
}
.redes{
    margin-top: 35px;
    text-align: center;
}
.redes i{
    font-size: 20px;
}
.redes a i{
    color: #e6e6e6;
}
.redes span{
    text-align: center;
    display: block;
    margin: auto;
}

/* BTN SWITCH LANGUAGE */
.btn-swicth-language{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    z-index: 10000;
}

.click-language{
    padding: 1em;
}
.btn-default{
    color: #036920;
    font-family: Anke;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 13px;
}
.btn-default:hover{
    color: #036920;
    font-family: Anke;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 13px;
}
h3.language{
    background: #036920;
    text-align: center;
    color: #e6e6e6;
    padding: 5px 0;
    font-size: 12px;
}
/*imagenes efecto*/
.customers li:hover img {-webkit-transform:scale(1.3);-ms-transform:scale(1.3);transform:scale(1.3);-webkit-filter: grayscale(80%);filter: grayscale(80%);-webkit-transition:all .5s ease-in-out;-o-transition:all .5s ease-in-out;transition:all .5s ease-in-out;}
.customers {overflow:hidden;}

@media only screen and (min-width: 360px) and (max-width: 640px){
    img.logo-spaceag.navbar-brand{
        margin-left: 55px;
    }
    ul.nav.navbar-nav.navbar-right{
        margin-right: 0;
        background: #e6e6e69d;
    }
    .navbar-inverse{
        width: 100%;
    }
    .spaceag{
        font-size: 4.3rem;
    }
    .subtitle{
        font-size: 25px;
    }
    h1.sections{
        font-size: 3.4rem;
    }
    h2{
        font-size: 2.9rem;
    }
    h3{
        font-size: 2rem;
    }
    p.textAbout{
        font-size:1.7rem;
    }
    div.contenido{
        text-align: center;
    }
    .row, .container-fluid{
        margin-bottom: 20px;
    }
    .data{
        height: 251px;
    }
    .sensores{
        height: 196px;
    }
    .counter{
        height: 296px;;
    }
    .customers img{
        width: 21%;
    }
    .media li img{
        width: 32%;
    }
    .blog-container{
        width: 69%;
        padding: 3px 42px 21px 42px;
    }
}
@media only screen and (min-width: 320px) and (max-width: 568px){
    img.logo-spaceag.navbar-brand{
        margin-left: 37px;
    }
    ul.nav.navbar-nav.navbar-right{
        margin-right: 0;
        background: #e6e6e69d;
    }
    .navbar-inverse{
        width: 100%;
    }
    .spaceag{
        font-size: 4rem;
    }
    .subtitle{
        font-size: 23px;
    }
    h1.sections{
        font-size: 3.2rem;
    }
    h2{
        font-size: 2.7rem;
    }
    h3{
        font-size: 1.8rem;
    }
    p.textAbout{
        font-size:1.6rem;
    }
    div.contenido{
        text-align: center;
    }
    .row, .container-fluid{
        margin-bottom: 20px;
    }
    .data{
        height: 251px;
    }
    .sensores{
        height: 196px;
    }
    .counter{
        height: 296px;;
    }
    .customers img{
        width: 25%;
    }
    .media li img{
        width: 25%;
        margin-left: -20px;
    }
    .blog-container{
        width: 69%;
        padding: 3px 42px 21px 42px;
    }
}
@media only screen and (min-width: 414px) and (max-width: 736px){
    div.contenido{
        text-align: left;
    }
}
@media only screen and (min-width: 768px) and (max-width: 1024px){
    ul.nav.navbar-nav.navbar-right{
        margin-right: -32px;
        background: #e6e6e68c;
        border-radius: 10px 0 0 10px;
    }
    div.contenido{
        text-align: center;
    }
    .data{
        top: 60px;
        height: 400px;
    }
    .sensores{
        top: 60px;
        height: 400px;
    }
    .counter{
        top: 30px;
        height: 400px;;
    }
    .redes{
        margin-left: -293px;
        width: 30%;
    }

}





